"use client";

import { useState, useRef, useEffect } from "react";
import Link from "next/link";
import { ChevronDown, Camera, FileUp, Edit3 } from "lucide-react";

interface MenuItem {
  icon: React.ReactNode;
  label: string;
  href: string;
  query?: Record<string, string>;
}

const menuItems: MenuItem[] = [
  {
    icon: <Camera className="h-4 w-4" />,
    label: "Tag billede",
    href: "/scan/camera",
    query: { redirect: "/wine-details" },
  },
  {
    icon: <FileUp className="h-4 w-4" />,
    label: "Upload billede",
    href: "/scan/upload",
    query: { redirect: "/wine-details" },
  },
  {
    icon: <Edit3 className="h-4 w-4" />,
    label: "Opret vin selv",
    href: "/wine-details",
  },
];

export function NewWineMenu() {
  const [isOpen, setIsOpen] = useState(false);
  const menuRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
        setIsOpen(false);
      }
    };

    if (isOpen) {
      document.addEventListener("mousedown", handleClickOutside);
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [isOpen]);

  return (
    <div className="relative" ref={menuRef}>
      {/* Dropdown Button */}
      <button
        onClick={() => setIsOpen(!isOpen)}
        className="flex items-center gap-2 rounded-[12px] bg-[#DC2626] px-5 py-2.5 text-sm font-semibold text-white shadow-[0_2px_8px_rgba(220,38,38,0.2)] transition-all duration-200 ease-out hover:bg-[#B91C1C] hover:shadow-[0_4px_12px_rgba(220,38,38,0.25)] active:scale-[0.98]"
        aria-label="Tilføj ny vin"
      >
        <span>Ny vin</span>
        <ChevronDown
          className={`h-4 w-4 transition-transform duration-300 ease-out ${
            isOpen ? "rotate-180" : ""
          }`}
        />
      </button>

      {/* Dropdown Menu */}
      {isOpen && (
        <div className="absolute right-0 top-full z-50 mt-2 w-52 rounded-[16px] border border-[#E5E7EB]/60 bg-white/95 backdrop-blur-xl shadow-[0_4px_20px_rgba(0,0,0,0.06),0_12px_40px_rgba(0,0,0,0.08)] overflow-hidden">
          {menuItems.map((item, index) => (
            <Link
              key={item.href}
              prefetch={false}
              href={{
                pathname: item.href,
                query: item.query,
              }}
              onClick={() => setIsOpen(false)}
              className="flex items-center gap-3 px-5 py-3.5 text-sm text-[#1f2937] transition-colors duration-150 hover:bg-[#F9FAFB] active:bg-[#F3F4F6]"
              style={{ borderTop: index > 0 ? '1px solid rgba(229, 231, 235, 0.5)' : 'none' }}
            >
              <div className="text-[#DC2626]">{item.icon}</div>
              <span className="font-medium">{item.label}</span>
            </Link>
          ))}
        </div>
      )}
    </div>
  );
}

