 "use client";

import Image from "next/image";
import Link from "next/link";
import { Badge } from "./ui/badge";
import type { Wine } from "@/lib/supabase/types";
import { format } from "date-fns";
import { da } from "date-fns/locale";
import { ReactNode, MouseEvent } from "react";

const wineTypeLabel: Record<Wine["type"], string> = {
  rød: "Rødvin",
  hvid: "Hvidvin",
  rosé: "Rosé",
  mousserende: "Mousserende",
};

interface WineCardProps {
  wine: Wine;
  actionSlot?: ReactNode;
}

export const WineCard = ({ wine, actionSlot }: WineCardProps) => {
  const preventNavigation = (event: MouseEvent) => {
    event.preventDefault();
    event.stopPropagation();
  };

  const cardContent = (
    <article className="group relative flex h-full flex-col overflow-hidden rounded-[20px] border border-[#E5E7EB]/50 bg-white transition-all duration-300 ease-out hover:shadow-[0_4px_20px_rgba(0,0,0,0.06),0_12px_40px_rgba(0,0,0,0.08)] hover:-translate-y-0.5">
      <div className="relative aspect-[4/5] overflow-hidden bg-[#F9FAFB]">
        {wine.image_url ? (
          <Image
            src={wine.image_url}
            alt={`${wine.producer} ${wine.vintage ?? ""}`.trim() || wine.producer}
            fill
            sizes="(min-width: 1024px) 320px, (min-width: 768px) 40vw, 100vw"
            className="object-cover object-[50%_60%] transition-transform duration-700 ease-out group-hover:scale-[1.03]"
          />
        ) : (
          <div className="flex h-full flex-col items-center justify-center gap-3 bg-gradient-to-br from-slate-100 via-slate-50 to-white text-center">
            <span className="text-xs font-medium uppercase tracking-[0.2em] text-slate-400">
              Ingen billede
            </span>
            <p className="px-8 text-sm text-slate-500/80">
              Denne vin er tilføjet uden foto.
            </p>
          </div>
        )}
        {/* Type badge overlay */}
        <div className="absolute bottom-5 left-5 z-10">
          <Badge className="border border-white/20 bg-white/95 backdrop-blur-md text-xs font-medium px-3 py-1.5 shadow-[0_1px_3px_rgba(0,0,0,0.02),0_2px_8px_rgba(0,0,0,0.04)] text-[#6B7280]">
            {wineTypeLabel[wine.type]}
          </Badge>
        </div>
        {actionSlot ? (
          <div
            className="absolute right-4 top-4 z-10 opacity-100 transition-opacity duration-200"
            onClick={preventNavigation}
          >
            {actionSlot}
          </div>
        ) : null}
      </div>

      <div className="flex flex-1 flex-col gap-6 p-7">
        {/* Producer - hovedtitel */}
        <div className="space-y-2">
          <h3 className="text-2xl font-semibold text-[#DC2626] leading-tight tracking-tight">
            {wine.producer}
          </h3>
          {/* Cuvée eller Appellation */}
          {(wine.cuvee || wine.appellation) && (
            <p className="text-base text-[#6B7280] leading-snug font-medium">
              {wine.cuvee || wine.appellation}
            </p>
          )}
        </div>

        {/* Details sektion - Region, Year */}
        <div className="space-y-3">
          {(wine.country || wine.wine_district) && (
            <div className="flex items-center gap-2.5 text-sm text-[#6B7280]">
              <span className="font-medium text-[#DC2626]">Region:</span>
              <span>{[wine.country, wine.wine_district].filter(Boolean).join(", ")}</span>
            </div>
          )}
          {wine.vintage && (
            <div className="flex items-center gap-2.5 text-sm text-[#6B7280]">
              <span className="font-medium text-[#DC2626]">Year:</span>
              <span>{wine.vintage}</span>
            </div>
          )}
        </div>

        {/* Detaljer: Druer, Alkohol, Vinmark, Dato */}
        <div className="mt-auto space-y-3 pt-5 border-t border-[#E5E7EB]/60">
          {wine.grapes && (
            <div className="grid grid-cols-[75px_1fr] items-center gap-3">
              <span className="text-sm text-[#6B7280] font-medium">Druer</span>
              <span className="text-base text-[#1f2937] font-medium">{wine.grapes}</span>
            </div>
          )}
          {wine.alcohol && (
            <div className="grid grid-cols-[75px_1fr] items-center gap-3">
              <span className="text-sm text-[#6B7280] font-medium">Alkohol</span>
              <span className="text-base text-[#1f2937] font-medium">{wine.alcohol}</span>
            </div>
          )}
          {wine.vineyard && (
            <div className="grid grid-cols-[75px_1fr] items-center gap-3">
              <span className="text-sm text-[#6B7280] font-medium">Vinmark</span>
              <span className="text-base text-[#1f2937] font-medium">{wine.vineyard}</span>
            </div>
          )}
          <div className="grid grid-cols-[75px_1fr] items-center gap-3">
            <span className="text-sm text-[#6B7280] font-medium">Dato</span>
            <span className="text-base text-[#1f2937] font-medium">
              {format(new Date(wine.created_at), "d. MMM yyyy", { locale: da })}
            </span>
          </div>
        </div>

        {/* Noter */}
        {wine.notes && (
          <p className="line-clamp-2 text-sm text-[#6B7280]/90 leading-relaxed">
            {wine.notes}
          </p>
        )}
      </div>
    </article>
  );

  return (
    <Link
      href={`/wines/${wine.id}/edit`}
      className="block focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-[#DC2626]/30 focus-visible:ring-offset-2 focus-visible:ring-offset-white rounded-[20px]"
    >
      {cardContent}
    </Link>
  );
};

