import { forwardRef } from "react";
import { Slot } from "@radix-ui/react-slot";
import type { ComponentPropsWithoutRef, ElementRef } from "react";
import { twMerge } from "tailwind-merge";

const baseStyles =
  "inline-flex items-center justify-center rounded-[12px] border border-transparent bg-[#DC2626] px-5 py-2.5 text-sm font-semibold text-white shadow-[0_2px_8px_rgba(220,38,38,0.2)] transition-all duration-200 ease-out hover:bg-[#B91C1C] hover:shadow-[0_4px_12px_rgba(220,38,38,0.25)] active:scale-[0.98] focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-[#DC2626]/30 disabled:pointer-events-none disabled:opacity-50 disabled:cursor-not-allowed";

type ButtonProps = ComponentPropsWithoutRef<"button"> & {
  asChild?: boolean;
};

export const Button = forwardRef<ElementRef<"button">, ButtonProps>(function Button(
  { className, asChild = false, ...props },
  ref,
) {
  const Component = asChild ? Slot : "button";

  return (
    <Component
      ref={ref}
      className={twMerge(baseStyles, className)}
      {...props}
    />
  );
});

